import React from 'react';
import styled from 'styled-components';
import Button from "../Atomic/Button";

const Text = styled.div`
    padding: 16px;
`;

const Form = styled.form`
    padding: 16px;
`;

const MultipleChoice = ({children, choices, solution}) => {
    const [state, setState] = React.useState(null);

    const constructAnswers = () => {
        const temp = [];

        for(let i = 0; i < choices.length; i++) {
            temp.push(<Radio key={i} value={i}>{choices[i]}</Radio>)
        }

        return temp;
    }

    const Radio = ({value, children}) => {
        return (
            <label>
                {children}
                <input onChange={handleChange} type={'radio'} value={value} checked={value === state} />
            </label>
        )
    };

    const handleChange = (event) => {
        setState(parseInt(event.target.value));
    }

    return (
        <>
            <Text>
                {children}

            </Text>
            <Form onSubmit={(event) => event.preventDefault()}>
                {constructAnswers()}
            </Form>
            <Button onClick={() => solution(state)}>Lösung abgeben.</Button>
        </>
    )
};

export default MultipleChoice;